import {
  animate,
  state,
  style,
  transition,
  trigger,
} from "@angular/animations";
import { Component, OnInit, ViewChild } from "@angular/core";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { TransactionService } from "../transaction.service";
import { Order, OrderResponse } from "../order";
import { FormControl, FormGroup } from "@angular/forms";
import { UserService } from "src/app/user/user.service";
import { HelperService } from "src/app/shared/services/helper.service";
@Component({
  selector: "app-order-history",
  templateUrl: "./order-history.component.html",
  styleUrls: ["./order-history.component.scss"],
  animations: [
    trigger("detailExpand", [
      state("collapsed", style({ height: "0px", minHeight: "0" })),
      state("expanded", style({ height: "*" })),
      transition(
        "expanded <=> collapsed",
        animate("225ms cubic-bezier(0.4, 0.0, 0.2, 1)")
      ),
    ]),
  ],
})
export class OrderHistoryComponent implements OnInit {
  companyArray: any[];
  statusArray: any[] = [
    { name: "All Transactions", code: "" },
    { name: "Pending Transactions", code: "pending" },
    { name: "Voided Transactions", code: "void" },
    { name: "Delevered Transactions", code: "delivered" },
    { name: "Cancelled Transactions", code: "cancelled" },
    { name: "Failed Transactions", code: "failed" },
    { name: "Insufficient Fund", code: "insufficient" },
    { name: "Ticket Printed Fund", code: "ticket-printed" },
    { name: "Not Pending Transactions", code: "not_pend" },
  ];
  downloading = false;
  range = new FormGroup({
    start: new FormControl(),
    end: new FormControl(),
  });
  dataSource: Order[] = [];
  tableLoading: boolean = true;
  // dataSource = new MatTableDataSource([]);
  title = "Order History";
  displayedColumns: any[] = [];
  expandedElement: any;
  length: number;
  private pageSize: number;
  private pageIndex: number;
  private startDate: string;
  private endDate: string;
  private status: string = "";
  private company: string = "";
  isStaff: boolean;
  isSid: boolean;
  isComp: boolean;
  isVend: boolean;
  isAcct: boolean;
  vendorArray: { name: any; id: any }[];
  vendor: any;
  isVoiding: any;
  userInfo: any;

  constructor(
    private transactionService: TransactionService,
    private userService: UserService,
    private helperService: HelperService
  ) { }
  @ViewChild(MatSort) sort: MatSort;

  ngOnInit(): void {
    this.pageSize = 10;
    this.tableLoading = true;
    this.startDate = this.endDate = this.helperService.formatDate(new Date());
    this.transactionService.getIsLoading()
      .subscribe((isLoading) => {
        isLoading ? null : this.getOrderHiistory()
      })
    this.getOrderHiistory();
    this.getuserGroup();
    this
  }

  getuserInfo() {
  }

  prepareForCancel(order) {
    this.userInfo = this.helperService.getUserInfo()
    const cancelling_grace_period_in_hours = this.userInfo.cancelling_grace_period_in_hours;
    const delievery_date = new Date(order.delivery_date)
    const now = new Date()
    // const cancel_date: Date = delievery_date.setHours(delievery_date.getHours() - cancelling_grace_period_in_hours)
    return now > delievery_date
  }

  getuserGroup() {
    const userGroup = this.helperService.getUserGroup();
    switch (userGroup) {
      case "emp":
        this.isStaff = true;
        this.displayedColumns = [
          // "position",
          // "comment",
          "food",
          "quantity",
          "platform",
          "vendor_user_id",
          "status",
          "star",
        ];
        break;
      case "ven":
        this.isVend = true;
        this.displayedColumns = [
          // "position",
          "user",
          "company",
          "food",
          "quantity",
          "platform",
          "comment",
          "status",
          "star",
        ];
        // this.getCompanyArray()
        break;
      // case 'sid':
      //   this.isSid = true;
      //   this.getCompanyArray()
      //   break;
      case "cmp_adm":
        this.displayedColumns = [
          // "position",
          "user",
          "vendor",
          "food",
          "quantity",
          "platform",
          "comment",
          "status",
          "star",
        ];
        this.isComp = true;
        this.getVendorArray();
        break;
      case "cmp_act":
        this.displayedColumns = [
          // "position",
          "user",
          "vendor",
          "food",
          "quantity",
          "platform",
          "comment",
          "status",
          "star",
        ];
        this.isAcct = true;
        this.getVendorArray();
        break;
      case "sid":
        this.isSid = true;
        this.displayedColumns = [
          "company",
          "user",
          "vendor",
          "food",
          "quantity",
          "platform",
          "comment",
          "status",
          "star",
        ];
        this.getCompanyArray();
        this.getVendorArray();
        break;

      default:
        break;
    }
  }

  getVendorArray() {
    this.userService.getVendors();
    this.userService.getVendorsUpdate().subscribe((vendors) => {
      const venArray = vendors.results.map((element) => {
        return { name: element.bussiness_name, id: element.user.id };
      });
      this.vendorArray = [{ id: "", name: "All Vendors" }, ...venArray];
    });
  }

  getCompanyArray() {
    this.userService.getCompanyList();
    this.userService.getCompanyListUpdate().subscribe((company) => {
      const comArray = company.results.map((element) => {
        return { name: element.name, id: element.id };
      });
      this.companyArray = [{ id: "", name: "All Companies" }, ...comArray];
    });
  }

  getOrderHiistory() {
    this.transactionService
      .getOrderHistory(
        this.startDate,
        this.endDate,
        this.status,
        this.company,
        this.vendor,
        this.pageSize,
        this.pageIndex
      )
      .subscribe((orderHistory: OrderResponse) => {
        if (orderHistory.results) {
          this.length = orderHistory.count;
          this.dataSource = orderHistory.results.map((element, index) => {
            return { ...element, position: index + 1 };
          });
          this.tableLoading = false;
        } else {
          this.length = 0;
          this.dataSource = [];
          this.tableLoading = false;
        }
        console.log(this.dataSource);
        // this.dataSource = new MatTableDataSource(orderHistory);
        // this.dataSource.sort = this.sort;
      });
  }

  pageChange(value: { pageSize: number; pageIndex: number }) {
    this.pageSize = value.pageSize;
    this.pageIndex = value.pageIndex;
    this.getOrderHiistory();
    this.tableLoading = true;
  }

  onExportExcel() {
    this.downloading = !this.downloading;
    let data;
    if (this.isStaff) {
      data = this.dataSource.map((element) => {
        return {
          name: element?.food?.name,
          quantity: element?.quantity,
          platform: element?.platform,
          vendor_user_id: element?.vendor_user_id_full?.bussiness_name,
          status: element?.status,
        };
      });
    } else if (this.isComp) {
      data = this.dataSource.map((element) => {
        return {
          user: element?.user,
          vendor_user_id: element?.vendor_user_id,
          name: element?.food,
          quantity: element?.quantity,
          platform: element?.platform,
          comment: element?.vendor_user_id_full?.bussiness_name,
          status: element?.status,
        };
      });
    } else if (this.isVend) {
      data = this.dataSource.map((element) => {
        return {
          user: element?.user,
          company: element?.company,
          name: element?.food,
          quantity: element?.quantity,
          platform: element?.platform,
          comment: element?.vendor_user_id_full?.bussiness_name,
          status: element?.status,
        };
      });
    } else if (this.isSid) {
      data = this.dataSource.map((element) => {
        return {
          company: element?.company,
          user: element?.user,
          vendor_user_id: element?.vendor_user_id,
          name: element?.food,
          quantity: element?.quantity,
          platform: element?.platform,
          comment: element?.vendor_user_id_full?.bussiness_name,
          status: element?.status,
        };
      });
    }
    this.helperService.exportExcel(data, "transactions", this.displayedColumns);
    this.downloading = !this.downloading;
  }

  statusChange(value) {
    this.status = value;
    this.getOrderHiistory();
    this.tableLoading = true;
  }

  companyChange(value) {
    this.company = value;
    this.getOrderHiistory();
    this.tableLoading = true;
  }

  setStartDate(value) {
    this.startDate = this.helperService.formatDate(value.value);
  }

  setEndDate(value) {
    this.endDate = this.helperService.formatDate(value.value);
    this.getOrderHiistory();
    this.tableLoading = true;
  }

  vendorChange(value) {
    this.vendor = value;
    this.getOrderHiistory();
    this.tableLoading = true;
  }

  onVoidTransaction(transactionId) {
    this.isVoiding = true;
    this.transactionService.void(transactionId).subscribe((isLoading) => {
      this.isVoiding = isLoading;
      isLoading ? this.getOrderHiistory() : null;
    });
  }

  onCancelOrder(food) {
    this.transactionService.cancelOrder(food)
  }
}
